*{
    scroll-behavior: smooth;
}


.home{
    width: 100%;
    overflow: hidden;
    margin-bottom: 20px;

}

.mainPage{
    background-color: rgb(15, 14, 14);
}
.singleData{
    width: 35%;
    position: relative;
    top: 200px;
    left: 3.6%;
    /* position: relative;
    top: -2px;
    background: transparent; */
}
.singleTitle{
    font-size: 30px;
    font-weight: bolder;
}
.singleDescribe{
    font-size: 20px;
}
.head{
    color: white;
    margin-top: 400px;
    margin-bottom: 20px;
}
.singleButtons{
    position: relative;
    top: 210px;
    left: 3.5%;
    display: flex;
    flex-direction: row;
    gap: 10px;
}
.playSingle{
    padding: 10px 30px;
    font-size: 17px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 5px;
    background-color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.singleInfo{
    padding: 10px 30px;
    font-size: 17px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 5px;
    background-color: rgb(100, 98, 98);
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.moreDatas{
    position: relative;
    left: 5%;
}
.homeMovies{
    display: flex;
    flex-direction: row;
    /* overflow: hidden; */
    gap: 5px;
    cursor: pointer;
    transition: all 0.6s ease-in-out;
    margin-bottom: 15px;
}
.movies:hover{
    transition: all 0.3s ease;
    transform: scale(1.25);
}

.movies img{
    width: 210px;
    height: 250px;
}

.leftHome{
    position: absolute;
    width: 40px;
    height: 85%;
    right: 103%;
    top: 40px;
    font-size: 30px;
    background: transparent;
    border: none;
    border-top: 10px;
    color: black;
}
.leftHome:hover{
    cursor: pointer;
    background-color:rgba(0, 0, 0, 0.5);
    color: white;
    transition: all 0.6s ease-in-out;
}
.right{
    position: absolute;
    width: 40px;
    height: 85%;
    top: 40px;
    left: 93%;
    font-size: 30px;
    background: transparent;
    border: none;
    border-top: 10px;
}
.right:hover{
    cursor: pointer;
    background-color:rgba(0, 0, 0, 0.5);
    color: white;
    transition: all 0.6s ease-in-out;
}



